$default-font: "GothamPro", sans-serif;
$accent: #568F0A;
$text: #606060;
$wcolor: #fff;
$bcolor: #000;



.btn {
	position: relative;
	padding: 3px 20px;
	display: flex;
	align-items: center;
	border: 2px solid #ffffff;
	white-space: nowrap;
	overflow: hidden;
	transition: .5s;
	box-sizing: border-box;
	&::before {
		content: '';
		position: absolute;
		left: 0;
		top: -1px;
		width: 100%;
		height: 0;
		background-color: #fff;
		transition: .8s;
		z-index: -1;
		border-radius: 0 0 50% 50%; }
	svg {
		margin-right: 15px; }
	&:hover {
		border: 2px solid darken($accent, 0);
		color: $accent;
		cursor: pointer;
		box-sizing: border-box;
		&::before {
			height: 190%; }
		svg {
			use {
				fill: $accent; } } } }
.btn-empty {
	position: relative;
	padding: 3px 20px;
	display: flex;
	align-items: center;
	white-space: nowrap;
	overflow: hidden;
	transition: .5s;
	&::before {
		content: '';
		position: absolute;
		left: 0;
		bottom: -1px;
		width: 100%;
		height: 0;
		background-color: #fff;
		transition: .8s;
		z-index: -1;
		border-radius: 50% 50% 0 0; }
	&:hover {

		color: $accent;
		cursor: pointer;
		&::before {
			height: 190%; }
		svg {
			use {
				fill: $accent; } } } }
.article-mt {
	margin-top: 40px; }
.color-accent {
	color: $accent; }
.color-accent-block {
	display: block;
	color: $accent; }


