			margin-bottom: 20px; } }
	&__btn {
		position: absolute;
		top: 115px;
		right: -130px;
		width: 130px;
		height: 130px;
		background-color: #efefef;
		transform: translateY(-50%);
		display: flex;
		justify-content: center;
		align-items: center;
		&-text {
			position: relative;
			margin-left: 20px;
			color: $accent;
			font-size: 12px;
			font-weight: 500;
			letter-spacing: 0.12px;
