				position: absolute;
				top: -7px;
				width: 42px;
				fill: $accent;
				stroke: $accent; }
			.leaves-w {
				position: absolute;
				top: 10%;
				left: -45px;
				width: 42px;
				transform: translateY(-50%);
				fill: $accent;
				stroke: $accent; } }

		&:hover {
			cursor: pointer; } } }

.article-slider {
	&__nav {
		position: absolute;
		display: flex;
		top: -44px;
		left: 0;
		width: 130px;
		height: 44px;
		background-color: #efefef;
		&-left {
			flex-basis: 50%;
			display: flex;
			justify-content: center;
			align-items: center;
			svg {
				width: 30px;
				stroke: $accent;
				stroke-width: 3px;
				transform: rotate(180deg); }
			&:hover {
				cursor: pointer; } }
		&-right {
			flex-basis: 50%;
			display: flex;
			justify-content: center;
			align-items: center;
			svg {
				width: 30px;
				stroke: $accent;
				stroke-width: 3px; }
			&:hover {
				cursor: pointer; } } }
	.article__descr {
		padding: 0; }
	.article__text {
		position: relative;
		&-top {
			padding: 26px 44px;
			padding-right: 110px;
			margin: 0;
			border-bottom: 1px solid #efefef; }
		&-bottom {
			padding-top: 20px;
			padding-bottom: 40px;
			padding-right: 70px;
			margin: 0;
			span {
				margin-bottom: 6px;
				font-size: 20px;
				letter-spacing: 0.2px; } } }
	.slider__count {
		position: absolute;
		top: 32px;
		left: 2px;
		color: $accent;
		font-size: 30px;
		font-weight: 400;
		line-height: 26px;
		letter-spacing: 0.3px; } }



