	.bannerDescr {
		display: flex;
		flex-direction: column;
		align-items: center;
		font-size: 14px;
		color: #fff;
		&__text {
			margin-bottom: 25px;
			font-weight: 400;
			line-height: 26px;
			letter-spacing: 0.14px; }

		&__btn {
			font-weight: 500;
			line-height: 31px;
			letter-spacing: 0.7px; } } }


@import "media"; // Always at the end
